<template>
  <div>
    <div class="selectSheduleContainer">
      <select class="weeksShedule" id="my-dropdown" v-model="selectedWeek">
        <option value="thisWeek">Розклад цього тижня {{ thisWeekSteamy ? '(Парний)' : '(Непарний)' }}</option>
        <option value="nextWeek">Розклад наступного тижня {{ !thisWeekSteamy ? '(Парний)' : '(Непарний)' }}</option>
      </select>
    </div> 
    <div class="scheduleContainer">
      <div class="scheduleElement" v-for="(daySchedule, day) in (selectedWeek === 'thisWeek' ? (thisWeekSteamy ? steamyWeekScheduleAT212 : unpairWeekScheduleAT212) : (thisWeekSteamy ? unpairWeekScheduleAT212 : steamyWeekScheduleAT212))" :key="day">
        <h2>{{ day }}</h2>
        <ul>
          <li v-for="lesson in daySchedule" :key="lesson.name">
            <a >{{ lesson.name }}</a>
          </li>
        </ul>
      </div>
      <div class="scheduleElement">
        <h2>Розклад дзвінків</h2>
        <ul>
          <li>
            <a >Перша пара:&nbsp;&nbsp;&nbsp;{{ 5+utc }}:00 - {{ 6+utc }}:35</a>
          </li>
          <li>
            <a >Друга пара:&nbsp;&nbsp;&nbsp;{{ 6+utc }}:50 - {{ 8+utc }}:25</a>
          </li>
          <li>
            <a >Третя пара: {{ 8+utc }}:40 - {{ 10+utc }}:15</a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template> 
<script>
  
  export default {
    data(){
      return{
        selectedWeek: 'thisWeek'
      }
    },
    props: ['thisWeekSteamy','unpairWeekScheduleAT212','steamyWeekScheduleAT212','utc'],
    mounted(){
      
    },
    methods: {
      
    }
    
  }

  
</script>
