  <div class="scheduleAppContainer" id="app">
    <header>
      <div class="full-wrapper-header">
        <div class="menu-btn-wrapper">
          <div class= "menu-btn" :class="[(burgerStatus) ? 'menu-btn-active' : '']" @click="toggleBurger">
            <div class="menu-btn-element"></div>
            <div class="menu-btn-element"></div>
            <div class="menu-btn-element"></div>
          </div>
