          </a>
        </li>
        <li class="burger-list-item" v-if="!settingsStatus">
          <a href="https://drive.google.com/drive/folders/19scLPY0CfzhsKBOzdcyEQaqOaE8qif9s?usp=share_link" @click="toggleBurger">
            Матеріали ВМ
          </a>
        </li>
        <li class="burger-list-item" v-if="!settingsStatus">
          <a href="https://drive.google.com/drive/folders/1e-d2un0Dn1kaJq6KGdUH5-KsXS-qb2Hd" @click="toggleBurger">
            Матеріали Метрологія
          </a>
        </li>
        <li class="burger-list-item" v-if="!settingsStatus">
          <a href="#" @click="toggleSettings">Налаштування</a>
        </li>
        <div class="settings-block" v-if="settingsStatus">
          <button class="close-settings-button" @click="toggleSettings">
            Назад
          </button>
          <div class="time-setting">
            <span>Встановлення часового поясу:</span>
            <select id="my-dropdown" v-model="utc">
              <option value='-4'>UTC - 04:00</option>
              <option value='-3'>UTC - 03:00</option>
              <option value='-2'>UTC - 02:00</option>
              <option value='-1'>UTC - 01:00</option>
              <option value='0'>UTC + 00:00</option>
              <option value='1'>UTC + 01:00</option>
              <option value='2'>UTC + 02:00</option>
              <option value='3'>UTC + 03:00</option>
              <option value='4'>UTC + 04:00</option>
              <option value='5'>UTC + 05:00</option>
              <option value='6'>UTC + 06:00</option>
              <option value='7'>UTC + 07:00</option>
              <option value='8'>UTC + 08:00</option>
            </select>
          </div>
        </div>
      </ul>
    </div>
   
   <div class="full-wrapper">
    <div class="dateContainer">
      <span class="title">Сьогодні:  {{ new Date().getDate() >= 10 ? new Date().getDate() : '0' + new Date().getDate()}}.{{ (new Date().getMonth() + 1) >=10 ? (new Date().getMonth() + 1) : '0' + (new Date().getMonth() + 1)}} {{ getDay }}.
      <br> {{ thisWeekSteamy ? 'Парний' : 'Непарний' }} ({{ this.getWeekNumber }}) тиждень</span>
    </div>

    <LiveSchedule :group="selectedOption" :thisWeekSteamy = "thisWeekSteamy" :unpairWeekScheduleAT212="getUnpairWeekScheduleAT212" :steamyWeekScheduleAT212="getSteamyWeekScheduleAT212" :utc="utc"/>
    <div class="buttonOpenSheduleContainer">
      <button @click="showContent = !showContent">{{ !showContent ? "Відкрити" : "Закрити"}} повний розклад</button>
    </div>
    <div v-if="showContent">
      <div v-if="selectedOption == 'AT212'">
        <ScheduleAT212 :thisWeekSteamy = "thisWeekSteamy" :unpairWeekScheduleAT212="getUnpairWeekScheduleAT212" :steamyWeekScheduleAT212="getSteamyWeekScheduleAT212" :utc="getUtc"/>
      </div>
      <div v-if="selectedOption == 'AT211'">
        <ScheduleAT211 />
      </div>
    </div>
   </div>
    
    

  </div>
</template>

<script>
  import LiveSchedule from "./components/LiveSchedule.vue";
  import ScheduleAT211 from "./components/ScheduleAT211.vue";
  import ScheduleAT212 from "./components/ScheduleAT212.vue";

  const lessonById = {
  "0": {
    "lessonName": "Комп'ютерна електроніка ч.2",
    "link": "https://meet.google.com/ked-mfqb-kyb"
  },
  "1": {
    "lessonName": "Вища математика",
    "link": "https://meet.google.com/aoi-bhgs-zqt?pli=1&authuser=1"
  },
  "2": {
    "lessonName": "Конфліктологія (Практика)",
    "link": "https://meet.google.com/nvd-qykh-kxo?authuser=1"
  },
  "3": {
    "lessonName": "Конфліктологія (Лекція)",
    "link": "https://meet.google.com/evc-unoe-grn?pli=1&authuser=1"
  },
  "4": {
    "lessonName": "WEB-програмування",
    "link": "https://us04web.zoom.us/j/4818920588?pwd=MUxUZlgrbytudllrTnFmVVFvLzQvZz09"
  },
  "5": {
    "lessonName": "Основи стандартизації, взаємозамінності та метрології",
    "link": "https://meet.google.com/row-ybpc-bns"
  },
  "6": {
    "lessonName": "Філософія",
    "link": "https://us04web.zoom.us/j/5268770607?pwd=QXBSR1RtM0hKWWxtUmw3NHRoV29lQT09#success"
  },
  "7": {
    "lessonName": "Теорія автоматичного управління",
    "link": "https://us04web.zoom.us/j/78027756038?pwd=3UBI5b0J2STGNzAYFv1ESSKnIOAujr.1"
  },
  "8": {
    "lessonName": "Фізичне виховання",
    "link": null
  }
  };

  const scheduleAT212 = {
          "Понеділок": [
            { name: lessonById[0].lessonName + ' (Лекція)', link: lessonById[0].link },
            { name: lessonById[1].lessonName + ' (Практика)', link: lessonById[1].link },
            { name: lessonById[7].lessonName + ' (Лекція)', link: lessonById[7].link }
          ],
          "Вівторок": [
            { name: lessonById[8].lessonName, link: lessonById[8].link },
            { name: "Пари не буде", link: null },
            { name: lessonById[7].lessonName + ' (Практика)', link: lessonById[7].link }
          ],
          "Середа": [
            { name: lessonById[3].lessonName, link: lessonById[3].link },
            { name: lessonById[4].lessonName, link: lessonById[4].link },
            { name: lessonById[5].lessonName + ' (Лекція)', link: lessonById[5].link  }
          ],
          "Четвер": [
            { name: lessonById[4].lessonName + ' (Практика)', link: lessonById[4].link },
            { name: lessonById[6].lessonName + ' (Лекція)', link: lessonById[6].link },
            { name: lessonById[5].lessonName + ' (Лабораторна)', link: lessonById[5].link }
          ],
          "П`ятниця": [
            { name: lessonById[8].lessonName, link: lessonById[8].link },
            { name: lessonById[1].lessonName + ' (Консультація)', link: lessonById[1].link },
            { name: lessonById[0].lessonName + ' (Практика)', link: lessonById[0].link }
          ],
          
        };


  export default {
    data() {
      return {
        selectedOption: 'AT212',
        showContent: null,
        utc: localStorage.utc ? localStorage.utc : '3',
        userTheme: "light-theme",
        burgerStatus: false,
        settingsStatus: false,
      }
    },

    components: {
      LiveSchedule,
      ScheduleAT211,
      ScheduleAT212,
    },
    computed: {
      thisWeekSteamy() {
        if(this.getWeekNumber%2==0) 
          return 1;
        else
          return null;
      },
      getUnpairWeekScheduleAT212(){
        return scheduleAT212;
      },
      getUtc(){
        return parseInt(this.utc)
      },
      getSteamyWeekScheduleAT212(){
        let steamyWeekScheduleAT212 = JSON.parse(JSON.stringify(scheduleAT212));
  
        steamyWeekScheduleAT212["Вівторок"][1].name=lessonById[2].lessonName;
        steamyWeekScheduleAT212["Вівторок"][1].link=lessonById[2].link;

        steamyWeekScheduleAT212["Середа"][0].name="Пари не буде";
        steamyWeekScheduleAT212["Середа"][0].link=null;

        steamyWeekScheduleAT212["Четвер"][2].name=lessonById[6].lessonName + ' (Практика)';
        steamyWeekScheduleAT212["Четвер"][2].link=lessonById[6].link;
        return steamyWeekScheduleAT212;
      },
      getWeekNumber(){
        const today = new Date();
        const startOfYear = new Date(today.getFullYear(), 0, 1);
        const weekNumber = Math.ceil(((today - startOfYear) / 86400000 + startOfYear.getDay() + 1) / 7)-9;
        return weekNumber;
      },
      getDay(){
        const daysOfWeek = ['неділя', 'понеділок', 'вівторок', 'середа', 'четвер', 'п`ятниця', 'субота'];
        const date = new Date();
        const currentDayOfWeek = daysOfWeek[date.getDay()];
        return currentDayOfWeek
      },
      

      
    },
    watch: {
      selectedOption(newSelectedOption) {
        localStorage.selectedOption = newSelectedOption;
      },
      showContent(newShowContent) {
        localStorage.showContent = newShowContent;
      },
      utc(newUtc) {
        localStorage.utc = newUtc;
      }
    },
    mounted() {
      if (localStorage.selectedOption ) {
        this.selectedOption = localStorage.selectedOption;
      }
      if (localStorage.showContent) {
        this.showContent = localStorage.showContent === 'true';
      }
      const initUserTheme = this.getTheme() || this.getMediaPreference();
      this.setTheme(initUserTheme);
    },
    methods: {
      setTheme(theme) {
        localStorage.setItem("user-theme", theme);
        this.userTheme = theme;
        document.documentElement.className = theme;
        (theme === "light-theme") ? document.getElementById("switchTheme").checked = true 
          : document.getElementById("switchTheme").checked = false;
      },
      toggleTheme() {
        const activeTheme = localStorage.getItem("user-theme");
        (activeTheme === "light-theme") ? this.setTheme("dark-theme") : this.setTheme("light-theme");
      },
      getMediaPreference() {
        const hasDarkPreference = window.matchMedia("(prefers-color-scheme: dark)").matches;
        return (hasDarkPreference) ? "dark-theme" : "light-theme";
      },
      getTheme() {
        return localStorage.getItem("user-theme");
      },
      toggleBurger() {
        (this.burgerStatus) ? this.burgerStatus = false : this.burgerStatus = true;
      },
      toggleSettings() {
        (this.settingsStatus && this.burgerStatus) ? this.settingsStatus = false : this.settingsStatus = true;
      }
    },
    
};
</script>

