<template>
  <div v-if=" this.group == 'AT212'" class="scheduleContainer scheduleContainerTop">
    <!-- Раздел для сегодняшнего расписания -->
    <div  class="fast-travel-container">
      <div class="fast-travel-title-container">
        <span class="fast-travel-title">{{ this.updateWhenPairWord }}</span>
        <span class="fast-pair-name"> {{ this.getNameCurrentPair }}</span>
      </div>
      <div class="fast-travel-button-container">
        <span class="pair-start-time">{{ this.getStartAndEndPairTime[0] }}</span>
        <a class="fast-pair-link" :href="this.getLinkCurrentPair"><span>{{ this.getCoupleButton }}</span></a>
        <span class="pair-end-time">{{ this.getStartAndEndPairTime[1] }}</span>
      </div>
    </div>
    <div class="scheduleElement" v-if="todaysSchedule.today.length">
      <h2>Розклад на сьогодні:</h2>
      <ul>
        <li v-for="lesson in todaysSchedule.today" :key="lesson.name">      
          <a v-if="lesson.link != null" :href="lesson.link">{{ lesson.name }}</a>
          <a v-else>{{ lesson.name }}</a>
        </li>
      </ul>
    </div>
    <div class="scheduleElement" v-else>
      <h2>Сьогодні вихідний:</h2>
      <ul>
        <li class="fieldNoPairs">      
          <a ></a>
        </li>
      </ul>
    </div>
    <!-- Раздел для расписания на завтра -->
    <div class="scheduleElement" v-if="todaysSchedule.tomorrow.length">
      <h2>Розклад на завтра:</h2>
      <ul>
        <li v-for="lesson in todaysSchedule.tomorrow" :key="lesson.name">      
          <a>{{ lesson.name }}</a>
        </li>
      </ul>
    </div>
    <div class="scheduleElement" v-else>
      <h2>Завтра вихідний</h2>
      <ul>
        <li class="fieldNoPairs">      
          <a ></a>
        </li>
      </ul>
    </div>
  </div>
  <div v-else>
    <!-- Add content for other groups if needed -->
  </div>
</template>

<script>

 
export default {
  data() {
    return {
    };
  },
  props: ['group', 'thisWeekSteamy','unpairWeekScheduleAT212','steamyWeekScheduleAT212','utc'],
 
  mounted(){
  },

  methods: {
  },

  computed: {
  getNameCurrentPair(){
    const pairNumber = this.getPairNumber;
    if(this.updateWhenPairWord !="Пари закінчилися")
      return this.todaysSchedule.today[pairNumber].name;
    else
      return ""
  },
  getLinkCurrentPair(){
    const pairNumber = this.getPairNumber;
    return this.updateWhenPairWord !="Пари закінчилися" && this.todaysSchedule?.today?.[pairNumber]?.link ? this.todaysSchedule.today[pairNumber].link : null;
  },
  getCoupleButton(){
    if(this.updateWhenPairWord !="Пари закінчилися")
      return this.todaysSchedule.today[this.getPairNumber].link ? "Перейти на пару" : "Посилання немає";
    else
      return "Посилання немає";
  },
  getStartAndEndPairTime(){
    const pairs = {
    "0": {
      "pairStart": `${5 + parseInt(this.utc)}:00`,
      "pairEnd": `${6 + parseInt(this.utc)}:35`
    },
    "1": {
      "pairStart": `${6 + parseInt(this.utc)}:50`,
      "pairEnd": `${8 + parseInt(this.utc)}:25`
    },
    "2": {
      "pairStart": `${8 + parseInt(this.utc)}:40`,
      "pairEnd": `${10 + parseInt(this.utc)}:15`
    },
    "3": {
      "pairStart": `${10 + parseInt(this.utc)}:30`,
      "pairEnd": `${12 + parseInt(this.utc)}:05`
    }
    };

    if(this.updateWhenPairWord !="Пари закінчилися")
      return [pairs[this.getPairNumber].pairStart, pairs[this.getPairNumber].pairEnd] ;
    else
      return ["",""];
  },

  todaysSchedule() {
    const daysOfWeek = ["Неділя", "Понеділок", "Вівторок", "Середа", "Четвер", "П`ятниця", "Суббота"];
    const now = new Date()
    //now.setDate(13); 
    const schedule = { today: [], tomorrow: [] };

    if (this.group === 'AT212') {
      const weekSchedule = this.thisWeekSteamy ? this.steamyWeekScheduleAT212 : this.unpairWeekScheduleAT212;
      schedule.today = weekSchedule[daysOfWeek[now.getDay()]] ?? [];
      schedule.tomorrow = weekSchedule[daysOfWeek[now.getDay()+1]] ?? [];
    }
    return schedule;
  },

  timeFormatter(number){
    return number + this.utc
  },

  updateWhenPairWord(){
    const now = Date.now();
    //const now = new Date().setHours(8, 40, 0, 0);
    if(now > this.getPairsEndTimes[this.todaysSchedule.today.length] || !this.todaysSchedule.today.length){
      return "Пари закінчилися";
    }else if(now < new Date().setHours(5+parseInt(this.utc), 0, 0, 0)){
      return "Далі:";
    }else{
      for (let i = 0; i < this.todaysSchedule.today.length; i++) {
        if (now > this.getPairsEndTimes[i] && now < new Date(this.getPairsEndTimes[i] + 15*60000)) {
          return "Далі:";
        }
      }
      return "Зараз:"
    }
  },

  getPairsEndTimes(){
    const pairEndTimes = [
      new Date().setHours(6+parseInt(this.utc), 35, 0, 0),
      new Date().setHours(8+parseInt(this.utc), 25, 0, 0),
      new Date().setHours(10+parseInt(this.utc), 15, 0, 0),
      new Date().setHours(12+parseInt(this.utc), 5, 0, 0)
    ];
    return pairEndTimes
  },
  getPairNumber() {
  const now = Date.now();
  const pairsEndTimes = this.getPairsEndTimes;
  //const now = new Date().setHours(8, 40, 0, 0);
  for (let i = 0; i < this.todaysSchedule.today.length; i++) {
    if (now <= pairsEndTimes[i]) {
      return i;
    }
  }
  return null;
  },
}

};
 
  
  
</script>